import { useCallback } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useDispatch } from 'react-redux'
import { updateUserStakedBalance, updateUserBalance } from 'state/actions'
import { stake, sousStake, restake } from 'utils/callHelpers'
import { useMasterchef, useSousChef } from './useContract'
import { fetchFarmUserDataAsync } from '../state/farms'

export const useStake = (type: number, pid: number) => {
  const dispatch = useDispatch()
  const { account, chainId } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleStake = useCallback(
    async (amount: string) => {
      await stake(masterChefContract, pid, amount, account)
      dispatch(fetchFarmUserDataAsync(chainId, account))
    },
    [account, chainId, dispatch, masterChefContract, pid],
  )

  return { onStake: handleStake }
}
export const useReStake = (type: number, pid: number) => {
  const dispatch = useDispatch()
  const { account, chainId } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleStake = useCallback(async () => {
    await restake(masterChefContract, pid, account)
    dispatch(fetchFarmUserDataAsync(chainId, account))
  }, [account, chainId, dispatch, masterChefContract, pid])

  return { onStake: handleStake }
}

export const useSousStake = (sousId) => {
  const dispatch = useDispatch()
  const { chainId, account } = useWallet()
  const sousChefContract = useSousChef(sousId)

  const handleStake = useCallback(
    async (amount: string) => {
      await sousStake(sousChefContract, amount, account)
      dispatch(updateUserStakedBalance(chainId, sousId, account))
      dispatch(updateUserBalance(chainId, sousId, account))
    },
    [account, chainId, dispatch, sousChefContract, sousId],
  )

  return { onStake: handleStake }
}

export default useStake
